#sidebar {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 100%;
  font-size: 0.8rem;
  border-right: 0.1rem solid white;
  /* applying scrollbar */
  &::-webkit-scrollbar {
    width: 0.1rem;
  }
  &::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  &::-webkit-scrollbar-thumb {
    background: #555;
  }
  &::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
}

#sidebar img:last-child {
  padding-top: 0.2rem;
  width: 1.25rem;
}

#sidebar hr {
  width: 9rem;
  margin: 1rem 0;
}

.side-heading {
  color: rgba(255, 255, 255, 0.5);
  font-weight: bold;
  letter-spacing: 1px;
}
